.button {
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    letter-spacing: 0.01em;
    border: navajowhite;
    padding: 8px 28px;
    color: #FFFFFF;
    background: #366EFF;
    box-shadow: 0 4px 18px rgba(54, 110, 255, 0.35), inset 0 1px 0 rgba(255, 255, 255, 0.3);
    border-radius: 30px;
    cursor: pointer;
}

.button a {
    color: inherit;
    text-decoration: inherit;
}

.authButton {
    width: 347px;
}

.whiteButton {
    background: white;
    color: black;
    box-shadow: 0 2px 10px rgba(109, 109, 109, 0.25), inset 0 1px 0 rgba(255, 255, 255, 0.3);
}

.withIcon {
    display: flex;
    align-items: center;
    box-shadow: 0 2px 10px rgba(109, 109, 109, 0.25), inset 0 1px 0 rgba(255, 255, 255, 0.3);
    background: white;
    color: black;
    padding-right: 20px;
    padding-left: 20px;
}

.withIcon img {
    max-width: 16px;
}

.withIcon span {
    margin-left: 7px;
}

.deleteButton {
    background: red;
}

.button:disabled {
    opacity: 0.5;
    filter: grayscale(1);
    cursor: default;
}

.loadingIcon {
    max-width: 16px;
}