.blockProfile {
    max-width: 1000px;
    margin: 0 auto;
}

.profileInfo {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 413px;
    background: #FFFFFF;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1), -1px -1px 2px rgba(0, 0, 0, 0.1);
    border-radius: 2px;
    padding: 27px 30px 36px;
    margin: 12px auto 0;
}

.title {
    height: 27px;
    font-weight: 700;
    font-size: 22px;
    line-height: 27px;
    margin-top: 27px;
}

.blockAvatar {
    width: 96px;
    height: 96px;
    margin-top: 30px;
}

.avatar {
    width: 96px;
    height: 96px;
    border-radius: 50%;
}

.blockName {
    margin-top: 17px;
}

.name {
    font-style: normal;
    font-weight: 500;
    font-size: 22px;
    line-height: 24px;
    cursor: pointer;
}

.blockEmail {
    margin-top: 14px;
    cursor:default;
}

.email {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    opacity: 0.5;
}

.blockButton {
    margin-top: 20px;
}

.button{
    width: 127px;
    height: 36px;
    background: #FCFCFC;
    box-shadow: 0 2px 10px rgba(109, 109, 109, 0.25), inset 0 1px 0 rgba(255, 255, 255, 0.3);
    border-radius: 30px;
    cursor: pointer;
    color: #000000;
    opacity: 0.8;
    letter-spacing: 0.01em;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
}





