.starsWrap{
    display: flex;
    justify-content: start;
    width: 100px;
}
.star{
    width: 16px;
    height: 16px;
}
.starGrayFill{
    filter: grayscale(100%);
    width: 16px;
    height: 16px;
}
.starsWrap button{
    border: none;
    background-color: transparent;
    cursor: pointer;
}