.wrapper {
    width: 100%;
}

.wrapper input {
    width: 100%;
    background: #FFFFFF;
    border: 1px solid #D9D9D9;
    border-radius: 2px;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    padding: 6px 17px;
}

.wrapper input::placeholder {
    opacity: 0.5;
}